<template>
  <div>
    <div id="xigua"></div>
  </div>
</template>

<script>
import mp4 from '@/assets/img/trailer.mp4'
export default {
  name: 'nsVideo',
  props: {
    src: {
        type: String,
        default: ''
    },
    autoplay: { // / 
        type: Boolean,
        default: false
    },
    disableProgress: { // 
        type: Boolean,
        default: false
    },
  },
  data(){
    return {
        player: null,
        flag: '',
    }
  },
  created(){},
  mounted(){
    this.player = new window.Player({
      id: 'xigua',
      url: this.src ? this.src : mp4,
      autoplay: true,
      // disableProgress: true,
      allowSeekPlayed: true,
      // rotateFullscreen: true,
    });
    this.player.once('ready', () => {
      // this.disableDraw();
    })
    this.player.on('pause', this.pause)
    this.player.on('play', this.play)
    this.$emit('player', this.player);
  
  },
  methods:{
    pause(){
      for(var i = 1;i<=this.flag;i++){
        clearInterval(i);
      }
    },
    play(){
      console.log('rnima')
      this.disableDraw();
    },
    disableDraw(){
      // ==================================================================
      // 
      this.flag = setInterval(() => {
        // 
        // fileTdkeyid
        let json = window.sessionStorage.getItem('fileId');
        let preTime = json ?JSON.parse(json) : 0;
        let nowTime = Math.round(this.player.currentTime)
        // 1
        console.log(nowTime, preTime);
        if(nowTime - preTime > 1.1) {
          this.player.currentTime = preTime;
          this.player.play();
        }
        // 
        if(nowTime > preTime) window.sessionStorage.setItem('fileId', JSON.stringify(Math.round(this.player.currentTime)));
      }, 100)
      // ==================================================================
    }
  }
}
</script>

